<template>

    <v-card-text>
        <v-text-field label="Decimal" ref="decInput" v-model="decValue" :rules="decRules" @focus="decChange = true; decValue = ''" @blur="decChange = false" required>
            <v-tooltip slot="prepend" color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="copyValue(decValue)">mdi-content-copy</v-icon>
                </template>
                <span>Copy to clipboard</span>
            </v-tooltip>
        </v-text-field>

        <v-text-field label="String" v-model="strValue" :rules="strRules" @focus="strChange = true; strValue = ''" @blur="strChange = false" required>
            <v-tooltip slot="prepend" color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="copyValue(strValue)">mdi-content-copy</v-icon>
                </template>
                <span>Copy to clipboard</span>
            </v-tooltip>
        </v-text-field>

        <v-text-field label="Hex" ref="hexInput" v-model="hexValue" :rules="hexRules" @focus="hexChange = true; hexValue = '0x'" @blur="hexChange = false" required>
            <v-tooltip slot="prepend" color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="copyValue(hexValue)">mdi-content-copy</v-icon>
                </template>
                <span>Copy to clipboard</span>
            </v-tooltip>
        </v-text-field>
    </v-card-text>

</template>

<script>

    import { mapState } from 'vuex';

    export default {
        name: 'Hex',

        data() {
            return {
                decValue: '',
                decRules: [ v => /^[0-9]*$/.test(v) || 'Must be an integer number' ],
                decChange: false,

                strValue: '',
                strRules: [],
                strChange: false,

                hexValue: '0x',
                hexRules: [
                    v => v.startsWith('0x') || v.startsWith('0X') || 'Must begin with "0x" or "0X"',
                    v => /^0[xX][A-Fa-f0-9]*$/.test(v) || 'Must be a hexadecimal value'
                ],
                hexChange: false
            }
        },

        computed: {
            ...mapState({
                'w3': state => state.chain.w3
            })
        },

        watch: {
            decValue(val) {
                this.$refs.decInput.validate();

                if (this.decChange && this.$refs.decInput.valid) {
                    this.hexValue = this.w3.utils.toHex(val);
                    this.strValue = val;
                }
            },

            strValue(val) {
                if (this.strChange) {
                    this.hexValue = this.w3.utils.utf8ToHex(val);
                    this.decValue = val;
                }
            },

            hexValue(val) {
                this.$refs.hexInput.validate();

                if (this.hexChange && this.$refs.hexInput.valid) {
                    this.decValue = this.w3.utils.hexToNumberString(val);
                    this.strValue = this.w3.utils.hexToUtf8(val);
                }
            },
        },

        methods: {
            copyValue(val) {
                if (navigator.clipboard) {
                    navigator.clipboard.writeText(val);
                }
            }
        }

    }

</script>
